import React from "react";
import { Link } from 'react-router-dom';

export default function Home() {
  return (
    <>
      <div style={{ backgroundColor: "#16003b" }} className="rounded-lg m-3 hidden sm:block">
        <br />
        <br />
        <center>
          <h1 className="text-2xl md:text-2xl xl:text-2xl whitespace-nowrap overflow-hidden "
            style={{
              color: "white",
              fontFamily: "Ubuntu, sans-serif",
            }}
          >
            "Tech Talks: Journey into the Infinite Possibilities of Technology!"
          </h1>
        </center>
        <br />
        <br />
      </div>
      <div className="grid grid-cols-1 sm:grid-cols-1 gap-4 m-3">
        <div
          className="bg-gray-300 p-4 rounded-lg"
          style={{ backgroundColor: "#1f1d36" }}
        >
          <center>
            <img
              src="/image-assets/profile-pic.png"
              alt=""
              srcset=""
              width="350px"
            />
          </center>
          <div className="m-3 md:m-2">
            <h2 className="font-family: 'Ubuntu', sans-serif text-center text-white text-sm md:text-xl">
              Hi, I'm Siddhartha, an enthusiastic Computer Science Undergrad
            </h2>
            <br />
            <p className="font-family: 'Ubuntu', sans-serif text-sm md:text-xl text-white sm:text-justify text-justify text-center">
              With a deep-rooted passion for all things related to technology, I
              find myself constantly intrigued by the ever-evolving landscape of
              the digital realm.
              As a self-proclaimed tech aficionado, I thrive on exploring and
              understanding the cutting-edge advancements that shape our future.
              From breakthrough innovations to emerging trends, my blog serves
              as a platform to share my insights and perspectives on the
              world of technology.
            </p>
            <br />
            <p className="font-family: 'Ubuntu', sans-serif text-center text-sm md:text-xl text-white text-justify">
              Join me on this exhilarating journey as I delve into the vast
              realm of technology, sharing my experiences, thoughts, and
              discoveries along the way.
              Together, let's unravel the intricacies of the tech world and
              embrace the excitement of the ever-changing future.
            </p>
          </div>
        </div>
      </div>
      {/* <div>
        <div className="mt-3 mx-3" style={{ backgroundColor: "#080202" }}>
          <center>
            <img src="/image-assets/gif.gif" alt="" width={'300px'} />
          </center>
        </div>
      </div> */}
      <div className="m-3 mt-0" style={{ backgroundColor: "#080202" }}>
        <nav>
          <br />
          <h1
            className="font-family: 'Ubuntu', sans-serif text-center text-2xl md:text-2xl xl:text-2xl whitespace-nowrap overflow-hidden"
            style={{
              fontFamily: "Ubuntu, sans-serif",
              color: "antiquewhite",
            }}
          >
            "Topic Trove"
          </h1>
          <h1
            className="font-family: 'Ubuntu', sans-serif text-center sm:text-2xl text-2xl md:text-2xl xl:text-2xl whitespace-nowrap overflow-hidden"
            style={{
              fontFamily: "Ubuntu, sans-serif",
              color: "antiquewhite",
            }}
          >
            Explore Blogs by Category
          </h1>
          <div style={{ display: "flex", justifyContent: "center" }}>
            <center className="flex flex-row items-center sm:flex-row">
              <Link
                style={{
                  color: "aliceblue",
                  fontFamily: "Ubuntu, sans-serif",
                }}
                to="/tech-blogs"
                className="m-5 block mb-2 sm:mr-4 text-xl"
              >
                Technical
              </Link>
              <Link
                style={{
                  color: "aliceblue",
                  fontFamily: "Ubuntu, sans-serif",
                }}
                to="/lifestyle-blogs"
                className="m-5 block mb-2 sm:mr-4 text-xl"
              >
                Lifestyle
              </Link>
              <Link
                style={{
                  color: "aliceblue",
                  fontFamily: "Ubuntu, sans-serif",
                }}
                to="/software-blogs"
                className="m-5 block mb-2 sm:mr-4 text-xl"
              >
                Development
              </Link>
            </center>
          </div>

        </nav>

      </div>

      <div style={{ backgroundColor: "#16003b" }} className="rounded-lg m-3">
        <br />
        <center>
          <h1
            className="text-center text-xl md:text-2xl xl:text-2xl whitespace-nowrap overflow-hidden"
            style={{
              color: "white",
              fontFamily: "'Ubuntu', sans-serif",
            }}
          >
            "Exploring the Horizons"<br /> A Glimpse into My Current Studies
          </h1>
        </center>
        <br />
      </div>
      <div className="rounded-lg m-3" style={{ backgroundColor: "#16003b" }} id="contact">
        <div className="rounded-lg p-5" style={{ backgroundColor: "#1f1d36" }}>
          <div className="grid grid-cols-1 sm:grid-cols-2 gap-4 m-1 p-2 rounded-lg" style={{ backgroundColor: "#1f1d36" }}>
            <img src="/image-assets/mern wall.jpg" alt="" width="800px" className="order-1 sm:order-1" />
            <p className="font-family:'Ubuntu', sans-serif text-justify text-sm md:text-xl text-white order-1 sm:order-2" >
              Exploring the Power of Full Stack Development: Exploring HTML, CSS, JavaScript, Node.js, Express, APIs,
              MongoDB, and React! From the foundational trio of HTML, CSS, and JavaScript to the dynamic backend using
              Node.js and Express, I've built my basic foundation. But it doesn't stop there. I've delved into the art of
              data integration, fetching information from APIs, and explored MongoDB for flexible data storage using NoSQL.
              And now, I'm diving into the captivating realm of single-page applications using React. Join me as we unlock
              the boundless possibilities of Full Stack Development. Get ready for regular updates, tutorials, and
              resources that will fuel your own coding adventures. Let's unleash the power of technology and create
              exceptional digital experiences together!
              <p className="text-sm md:text-xl">
                <br />Stay Tuned and Happy coding!
              </p>
            </p>
          </div>

        </div>
      </div>


    </>
  );
}
