import React, { useEffect, useState } from "react";
import "./App.css";
import Header from "./MyComponents/Header";
import Footer from "./MyComponents/Footer";
import Home from "./MyComponents/Home";
import Blog from "./MyComponents/Blog";
import Error from "./MyComponents/Error";
import Blog_New from "./MyComponents/Blog_New";
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';

function App() 
{
  const [techBlogs, setTechBlogs] = useState([]);
  const [lifestyleBlogs, setLifestyleBlogs] = useState([]);
  const [travelBlogs, setTravelBlogs] = useState([]);
  const [developmentBlogs, setDevelopmentBlogs] = useState([]);
  useEffect(() => {
    fetchTechBlogs();
    fetchLifestyleBlogs();
    fetchTravelBlogs();
    fetchDevelopmentBlogs();
  }, []);
  async function fetchDevelopmentBlogs() {
    try {
      const response = await fetch('/software-blogs');
      const blogData = await response.json();
      setDevelopmentBlogs(blogData);
    } catch (error) {
      console.error("Error retrieving blog posts:", error);
    }
  }
  async function fetchTechBlogs() {
    try {
      const response = await fetch('/tech-blogs');
      const blogData = await response.json();
      console.log(blogData);
      setTechBlogs(blogData);
    } catch (error) {
      console.error("Error retrieving blog posts:", error);
    }
  }
  async function fetchLifestyleBlogs() {
    try {
      const response = await fetch('/lifestyle-blogs');
      const blogData = await response.json();
      setLifestyleBlogs(blogData);
    } catch (error) {
      console.error("Error retrieving blog posts:", error);
    }
  }
  async function fetchTravelBlogs() {
    try {
      const response = await fetch('/travel-blogs');
      const blogData = await response.json();
      setTravelBlogs(blogData);
    } catch (error) {
      console.error("Error retrieving blog posts:", error);
    }
  }
  return (
    <>
      <Header />
      <Router>
        <Routes>
          <Route exact path="/" element={<Home />} />
          <Route
            path="/tech-blogs"
            element={<Blog_New blogs={techBlogs} />}
          />
          <Route
            path="/lifestyle-blogs"
            element={<Blog_New blogs={lifestyleBlogs} />}
          />
          <Route
            path="/travel-blogs"
            element={<Blog blogs={travelBlogs} />}
          />
          <Route
            path="/software-blogs"
            element={<Blog_New blogs={developmentBlogs} />}
          />
          <Route
            path="/discussion-forums"
            element={<Error />}
          />
        </Routes>
      </Router>
      <Footer />
    </>
  );
}
export default App;
