import React from 'react'
import './Blog_Styles.css'
export default function Blog({ blogs }) {
    return (
        <>
            {blogs.length === 0 ? (
                <p>No Blog Posts Found</p>
            ) : (
                blogs.map(blog => (
                    <div key={blog.id} style={{ backgroundColor: '#DDE6ED' }} className="rounded-lg m-3">
                        <div className="m-3">
                            <center>
                                <br />
                                <br />
                                <h1 className="heading-title text-2xl">
                                    <strong>{blog.heading}</strong>
                                </h1>
                                <img
                                    src={"https://drive.google.com/uc?id=" + blog.image}
                                    alt="blog.image"
                                    className="rounded-lg"
                                    width="800px"
                                />
                                <br />
                            </center>
                            <h1 className="heading-blog">{blog.subheading}</h1>
                            <div dangerouslySetInnerHTML={{ __html: blog.content }} />
                        </div>
                    </div>
                ))
            )}
        </>
    );
}
